export default {
	data() {
		return {
			value: '',
			list: [],
			active: false,
			activeIndex: 0,
			text: '全部',
			textShow: false
		}
	},
	onLoad() {

	},
	computed: {
		listData() {
			let list = JSON.parse(JSON.stringify(this.list))
			let newList = []

			// 点击 全部
			if (this.activeIndex === 0) {
				this.text = '全部'
				return list
			}
			// 点击 待办事项
			if (this.activeIndex === 1) {
				// checked = false
				list.forEach((item) => {
					if (!item.checked) {
						newList.push(item)
					}
				})
				this.text = '待办'
				return newList
			}
			// 点击 已完成
			if (this.activeIndex === 2) {
				// checked = ture
				list.forEach((item) => {
					if (item.checked) {
						newList.push(item)
					}
				})
				this.text = '已完成'
				return newList
			}
			return []
		},
	},
	methods: {
		// 打开输入框
		create() {
			if (this.active) {
				this.close()
			} else {
				this.open()
			}
		},
		// 打开 动画
		open() {
			this.active = true
			this.$nextTick(() => {
				setTimeout(() => {
					this.textShow = true
				}, 50)
			})
		},
		// 关闭动画
		close() {
			this.textShow = false
			this.$nextTick(()=>{
				setTimeout(() => {
					this.active = false
				}, 350)
			})
		},
		// 发布
		add() {
			console.log(this.value);
			if (this.value === '') {
				uni.showToast({
					title: "请输入内容",
					icon: 'none'
				})
				return
			}
			this.list.unshift({
				id: 'id' + new Date().getTime(),
				content: this.value,
				checked: false
			})
			this.value = ''
			this.close()
		},
		// 点击列表触发
		finish(id) {
			let index = this.list.findIndex((item) => item.id === id)
			console.log('我被点击了', this.list[index]);
			this.list[index].checked = !this.list[index].checked
		},
		tab(index) {
			this.activeIndex = index
		}
	}
}
